export default {
  data: function data() {
    return {
      activeName: "1",
      loading: false,
      pickerOptions: {
        shortcuts: [{
          text: "Last week",
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit("pick", [start, end]);
          }
        }, {
          text: "Last month",
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit("pick", [start, end]);
          }
        }, {
          text: "Last three months",
          onClick: function onClick(picker) {
            var end = new Date();
            var start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit("pick", [start, end]);
          }
        }]
      },
      search_txt: "",
      search_select: "",
      timeCreated: "",
      items: [],
      pageSizes: [10, 20, 50, 100],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_storeRowsPerPage") ? Number(localStorage.getItem("c_storeRowsPerPage")) : 10,
      total: 0
    };
  },
  watch: {
    $route: "gotoPage",
    search_checkbox: function search_checkbox() {
      this.search();
    }
  },
  created: function created() {
    this.initData();
  },
  methods: {
    /**
     * 初始化数据
     */
    initData: function initData() {
      this.getTable();
    },

    /**
     * 获取表格中的数据
     */
    getTable: function getTable(s) {
      var _this = this;

      this.loading = true;
      var params = {
        rowsPerPage: this.rowsPerPage,
        page: this.page,
        abnormalStatus: Number(this.activeName),
        invoiceCode: this.search_txt,
        timeCreatedFrom: (this.timeCreated || [])[0] || "",
        timeCreatedTo: (this.timeCreated || [])[1] || ""
      };

      if (this.search_select) {
        params.status = Number(this.search_select);
      }

      this.$apiCall("api.PurchaseOrder.findByCustomer", params, function (r) {
        _this.loading = false;

        if (r.ErrorCode == 9999) {
          _this.items = r.Data.Results;
          _this.total = Number(r.Data.Pagination.totalCount);
          _this.totalPage = Number(r.Data.Pagination.totalPage);
          if (!s) _this.$root.$children[0].$refs.mainScroll.wrap.scrollTop = 0;
        } else {
          _this.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },

    /**
     * 搜索框事件
     */
    search: function search() {
      if (this.$route.query.page == 1) {
        this.getTable();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_storeRowsPerPage", val);
      this.getTable();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? Number(this.$route.query.page) : 1;
      this.getTable();
    }
  }
};